import "./styles.css";
import menu from "./menu";
import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import Button from "@material-ui/core/Button";

const useStyles = makeStyles((theme) => ({
  button: {
    display: "block",
    marginTop: theme.spacing(2)
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120
  }
}));

export default function ControlledOpenSelect() {
  const classes = useStyles();
  const [age, setAge] = React.useState("");
  const [open, setOpen] = React.useState(false);

  const handleChange = (event) => {
    setAge(event.target.value);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleOpen = () => {
    setOpen(true);
  };

  return (
    <div>
      <FormControl className={classes.formControl}>
        <InputLabel id="demo-controlled-open-select-label">メニュー</InputLabel>
        <Select
          labelId="demo-controlled-open-select-label"
          id="demo-controlled-open-select"
          open={open}
          onClose={handleClose}
          onOpen={handleOpen}
          value={age}
          onChange={handleChange}
        >
          <MenuItem value={10}>メイン</MenuItem>
          <MenuItem value={20}>デザート</MenuItem>
          <MenuItem value={30}>ドリンク</MenuItem>
        </Select>
      </FormControl>

      <h1>メニュー</h1>
      {menus(0)}
      {menus(1)}
      {menus(2)}
      {menus(3)}
    </div>
  );
}

//指定された要素番号(index)のメニューを返します
function menus(index) {
  const items = [];
  const keys = Object.keys(menu[index]);
  const values = Object.values(menu[index]);
  for (let i = 0; i < values.length; i++) {
    items.push(
      <li>
        {keys[i]}:{values[i]}
      </li>
    );
  }
  return <ul>{items}</ul>;
}

